import * as React from 'react';
import { Link, graphql } from 'gatsby';

import Layout from '../components/layout';
import Seo from '../components/seo';

export default ({ data, location }) => {
  const product = data.product;
  const siteTitle = data.site.siteMetadata?.title || '';

  const title = product.frontmatter.title;
  const description = product.frontmatter.description || `${product.excerpt}`;
  const productImageUrl = product.frontmatter.productImageUrl;
  const datasheetUrl = product.frontmatter.datasheetUrl;
  const spiceModelUrl = product.frontmatter.spiceModelUrl;
  const purchaseInfoUrl = product.frontmatter.purchaseInfoUrl;
  const html = product.html;

  // If there's no purchase info URL, link to the support page as a fallback (will fallback in most cases)
  const purchaseButton = !!purchaseInfoUrl
    ? (<a className="btn btn-primary" href={purchaseInfoUrl}>Purchase</a>)
    : (<Link className="btn btn-primary" to="/support">Purchase</Link>);
  // If there's no datasheet URL, use a fallback component (disabled button for now)
  const downloadDatasheetButton = !!datasheetUrl
    ? (<a className="btn btn-secondary" href={datasheetUrl}>Download Datasheet</a>)
    : (<a className="btn btn-secondary disabled"><del>Download Datasheet</del></a>);
  // If there's no spice model URL, use a fallback component (disabled button for now)
  const downloadSpiceModelButton = !!datasheetUrl
    ? (<a className="btn btn-secondary" href={spiceModelUrl}>Download Spice Model</a>)
    : (<a className="btn btn-secondary disabled"><del>Download Spice Model</del></a>);

  return (
    <div>
      <Seo
        title={title}
        description={description}
      />
      <div className="container mt-5 pb-5">
        <div className="row justify-content-center">
          <div className="col-11 card pb-4 border-0">
            <div className="row">
              <div className="col p-4">
                <h1>{title}</h1>
              </div>
              <div className="col-auto py-2 px-3 text-end d-block">
                {/* TODO: replace with left chevron icon */}
                <Link to="/products">&#x2190; View all products</Link>
              </div>
            </div>

            <div className="row px-4">
              <div className="col-12 col-md-6">
                <p className="mb-3">
                  {description}
                </p>
                <img className="card-img-top img-fluid w-75" src={productImageUrl} alt={description} />
              </div>

              <div className="col-12 col-md-6">
                <div className="row">
                  <div className="col-12 mt-4 mt-md-0">
                    {purchaseButton}
                  </div>
                </div>

                <div className="row">
                  <div className="col-12 mt-4">
                    {downloadDatasheetButton}
                  </div>

                  <div className="col-12 mt-4">
                    {downloadSpiceModelButton}
                  </div>

                  <hr className="mt-4 w-75" />

                  <div className="col-12 mt-2">
                    <section
                      dangerouslySetInnerHTML={{ __html: html }}
                      itemProp="productContent"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export const pageQuery = graphql`
  query ProductPostBySlug(
    $id: String!
  ) {
    site {
      siteMetadata {
        title
      }
    }
    product: markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 110)
      html
      frontmatter {
        title
        description
        productImageUrl
        datasheetUrl
        spiceModelUrl
        purchaseInfoUrl
      }
    }
  }`;
